@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.active{
    background: #fff;
}
.offline{
    background: transparent;
}
.img{
    background: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: cover;
     
    background-position: center;
    object-fit: cover;
     
    
}
.menuActive{
    color:#ccc;
}
.menuOff{
    color:#fff;
}
.hero{
    background:url(/sportsquiz/static/media/hero.4f5c04a0.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height:700px;
}
.tennis {
    /* background-image: url('../Images/image.jpg') ; */
    /* background: #5f2c82;   
  background: -webkit-linear-gradient(to right, #49a09d, #5f2c82);   
  background: linear-gradient(to right, #49a09d, #5f2c82);   */
    /* background-image: linear-gradient( 135.9deg,  rgba(109,25,252,1) 16.4%, rgba(125,31,165,1) 56.1% ); */
    /* background-image: linear-gradient( 109.6deg,  rgba(48,207,208,1) 11.2%, rgba(51,8,103,1) 92.5% ); */
  
    background-image: radial-gradient(
      circle farthest-corner at 10% 20%,
      rgba(153, 103, 186, 1) 41.2%,
      rgba(0, 113, 227, 1) 90%
    );
    background-repeat: no-repeat;
    background-size: cover;
  
    height: 100vh;
    width: 100%;
    opacity: 0.9;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .quiz {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    background-color: #252d4a;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    min-height: 400px;
    /* height: min-content; */
    border-radius: 15px;
    width: 500px;
  }
  
  @media only screen and (max-width:  520px){
      .quiz{
        display:none;
      }
    }
  .question-text {
    font-size: 30px;
    margin-bottom: 20px;
    color: #fff;
  }
  .answer-section {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    gap: 20px;
    justify-content: space-between;
  }
  button {
    width: 200px;
    text-align: center;
    padding: 10px;
    border-radius: 5px;
    outline: none;
    border: none;
    /* background: #283048;  /* fallback for old browsers */
    /* background: -webkit-linear-gradient(to right, #859398, #283048);  /* Chrome 10-25, Safari 5.1-6 */
  
    /* background: linear-gradient(to right, #859398, #283048); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
    background-image: radial-gradient(
      circle farthest-corner at 10% 20%,
      rgba(153, 103, 186, 1) 41.2%,
      rgba(0, 113, 227, 1) 90%
    );
    color: #fff;
    cursor: pointer;
  }
  button:hover {
    background: #555e7d;
  }
  .submit {
    background: #555e7d;
    width:80%;
  }
  @media screen and (max-width:500px){
      .submit{
          width:95%;
      }
  }
  .submit:hover {
    transition: 0.5s all ease-in-out;
  }
  .result {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    background-color: #252d4a;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    min-height: 400px;
    /* height: min-content; */
    border-radius: 15px;
    width: 500px;
    color: #fff;
  }
  @media screen and (max-width:500px){
      .result{
          width:90%;
          font-size:14px;
      }
  }
  
  .form {
    display: flex;
    flex-direction: column;
    width: 400px;
  
    background-image: radial-gradient(
      circle farthest-corner at 10% 20%,
      rgba(153, 103, 186, 1) 41.2%,
      rgba(0, 113, 227, 1) 90%
    );
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    min-height: 250px;
    margin-bottom: 30px;
    border-radius: 15px;
  }
   
  input {
    width: 100%;
    border-radius: 5px;
    outline: none;
    border: none;
    padding: 15px;
    margin-bottom: 20px;
    background-color: #252d4a;
    color: #fff;
  }
  @media screen and (max-width:500px){
      .modal{
      height:100vh;
      }
      .form{
      width:99%;
      }
  }
   
   
